<template>
  <div class="login-page">
    <b-container>
      <Widget class="mx-auto" title="<h3 class='mt-0 fw-normal text-center'>Login to your Web App</h3>" customHeader>
        <p class="text-center login-info mb-0 mt">
          Use Facebook, Twitter or your email to sign in.
        </p>
        <p class="text-center login-info">
          Don't have an account? Sign up now!
        </p>
        <form class="mt" @submit.prevent="loginUser">
          <b-alert class="alert-sm" variant="danger" :show="(errorMessage !== 'OK' && errorMessage !== null)">
            <span v-if="errorMessage === 'badLogin'">
            {{errorText.pl.badLogin}}&hellip;
            </span>
            <span v-if="errorMessage === 'sessionExpired'">
            {{errorText.pl.sessionExpired}}&hellip;
            </span>
          </b-alert>
          <b-form-group label="Email" label-for="username-input">
            <b-input-group class="input-group-transparent">
              <b-input-group-text slot="prepend"><i class="fa fa-user text-white"></i></b-input-group-text>
              <b-form-input id="username-input"
                            class="input-transparent pl-0 chrome-autofill-color-override"
                            type="text"
                            v-model="form.email"
                            required
                            placeholder="Your Email" />
            </b-input-group>
          </b-form-group>
          <b-form-group label="Password" label-for="password-input">
            <b-input-group class="input-group-transparent">
              <b-input-group-text slot="prepend"><i class="fa fa-lock text-white"></i></b-input-group-text>
              <b-form-input id="password-input"
                            class="input-transparent pl-0 chrome-autofill-color-override"
                            type="password"
                            v-model="form.password"
                            required
                            placeholder="Your Password" />
            </b-input-group>
          </b-form-group>
          <div class="widget-middle-overflow bg-widget mt-4 px-4 py-3">
            <b-button class="btn-block btn-lg fs-normal" type="submit" variant="danger">
              <span class="login-circle"><i class="fa fa-caret-right"></i></span>
              Sign in
            </b-button>
            <a href="#" class="text-center text-gray w-100 d-block mt-4">Forgot Username or Password?</a>
          </div>
          <div class="widget-bottom-overflow">
            <b-button class="btn-block btn-lg fs-normal" type="cancel" variant="primary">
              <i class="fa fa-facebook-square mr-2 fa-lg"></i>
              Login with Facebook
            </b-button>
          </div>
        </form>
      </Widget>
    </b-container>
    <footer class="footer">
      <!-- Light Blue Vue Admin Dashboard Template - Made by <a href="https://flatlogic.com" target="_blank">Flatlogic</a> -->
    </footer>
  </div>
</template>

<script>
import Widget from '@/components/Widget/Widget';
import {mapState, mapActions } from 'vuex';
export default {
  name: 'LoginPage',
  components: { Widget },
  data() {
    return {
      errorText: {
        pl: {
          badLogin: "Login lub hasło są niepoprawne, proszę spróbować ponownie",
          sessionExpired: 'Twoja sesja wygasła, zaloguj się ponownie',
        } ,
      },
      errorMessage: 'OK',
      form: {
        email: '',
        name: '',
      }
    };
  },
  methods: {
     ...mapActions('auth', ['login']),
   async loginUser() {
      const username = this.form.email;
      const password = this.form.password;

      if (username.length !== 0 && password.length !== 0)
       {
          await this.login({email: username, password: password});
          var self = this;
          setTimeout(function() {
            self.errorMessage = window.localStorage.getItem('errorMessage'); 
            console.log(self.errorMessage); 
            window.localStorage.setItem('errorMessage', 'OK');
          }, 200);       
          // localStorage.removeItem('errorMessage');
      }
    },
  },
  created() {
     this.errorMessage = window.localStorage.getItem('errorMessage');
     console.log(this.errorMessage);
     window.localStorage.setItem('errorMessage', 'OK');
    //  localStorage.removeItem('errorMessage');
    if (window.localStorage.getItem('authenticated') === 'true') {
      this.$router.push('/app/dashboard/visits');
    }
  },

};
</script>

<style src="./Login.scss" lang="scss" scoped />
